
import { isScrollBottom } from "@/utils/comm.js";
import { mapGetters, mapActions, mapMutations } from "vuex";
import { api } from "@/utils/api.js";
import { wxPay } from "@/utils/wx_sdk.js";
import loading from "@/components/loading.vue";
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import XHeader from 'vux/src/components/x-header/index.vue'

export default {
name: "shop",
props: {},
components: {
Group,
Cell,
XButton,
Badge,
XHeader,
loading
},
data() {
return {
orderid: "",
ordersn: "",
infodata: "",
interval: "",
timeList: {},
isAfter: 0
};
},
created() {
this.showdata();
},
mounted: function() {
this.$nextTick(function() {

});
},
destroyed() {
window.onscroll = null;
},
methods: {

goodsInfo(goodsId) {
this.$router.push({
path: '/index/goodsInfoPindan',
query: {
goodsId
}
})
},
after(order_id){
this.$router.push({
path: '/centerFull/partner/aftertype',
query: {
order_id
}
})
},

afterInfo(refund_id) {
this.$router.push({
path: '/centerFull/orderFull/afterlistInfo',
query: {
refund_id
}
})

},

copyTxt(e) {
let that = this;
let text = e.currentTarget.dataset.ordersn;
this.$copyText(text).then(function(e) {
that.$vux.toast.text("复制成功", "top");
});
},

async quxiaoOrder(e) {
let that = this;
let orderid = e.currentTarget.dataset.orderid;
let deleteType = e.currentTarget.dataset.deletetype;
let confirmtext = "确认";
let canceltext = "取消";
let title = "";
let content = "";
if (deleteType == 1) {
title = "取消订单";
content = "真的要走吗？宝贝很抢手哦～";
confirmtext = "去意已决";
canceltext = "我再想想";
} else {
title = "删除订单";
content = "确认删除订单？";
confirmtext = "去意已决";
canceltext = "";
}
this.$vux.confirm.show({
title,
content,
confirmText: confirmtext,
cancelText: canceltext,
async onConfirm() {
that.$vux.loading.show({
text: "处理中..."
});
let data = {
plat: 3,
account: that.account,
token: that.token,
order_id: orderid,
type: deleteType
};
const [err, res] = await api.cancelorder(data);
that.$vux.loading.hide();
if (err) {
that.$vux.toast.text(err.msg);
return;
}
that.$vux.toast.text(res.msg);
this.$router.go(-1);
}
});
},

buyagain(e) {
var goodsId = e.currentTarget.dataset.goodsid;
this.$router.push({
path: '/index/goodsInfoPindan',
query: {
goodsId
}
})
},

async gobay(orderid) {
this.$vux.loading.show({
text: '支付中...'
})
let data = {
plat: 3,
account: this.account,
token: this.token,
order_id: orderid,
pay_code: 2,
openid: this.user.openid
}
const [err, res] = await api.orderunpaidpay(data);
if (err) {
that.$vux.toast.text(err.msg);
return;
}
if(res.code == 2000){
var selfData = res;
let success = res =>{
that.showdata();
}
let fail = err =>{

}
wxPay(this,{...res.data.pay_param,success,fail})
}
},

pindanquxiao() {
this.$vux.alert.show({
title: "暂时无法取消订单",
content: "发起拼单24小时后，若未拼单成功，将 自动取消订单并退款哦",
buttonText: "我知道了",
onShow() {
console.log("Plugin: I'm showing");
}
});
},

goShip() {},

async tixingFahuo(e) {
let that = this;
let order_id = e.currentTarget.dataset.orderid || "";
let data = {
plat: 3,
account: that.account,
token: that.token,
order_id
};
this.$vux.confirm.show({
title: "",
content: "催发货后商家将会收到提醒，只能提醒发货一次哦～",
async onConfirm () {
const [err, res] = await api.shipnotify(data);
if (err) {
that.$vux.toast.text(err.msg);
return;
}
that.$vux.toast.text(res.msg);
}
})
},

receiving(e) {
let that = this;
let order_id = e.currentTarget.dataset.orderid;
this.$vux.confirm.show({
async onConfirm() {
let data = {};
const [err, res] = await api.orderreceive(data);
if (err) {
that.$vux.toast.text(err.msg);
return;
}
that.$vux.toast.text(res.msg);
},
onCancel() {}
});
},

goeva(e) {
let orderid = e.currentTarget.dataset.orderid;
let itemid = e.currentTarget.dataset.itemid;
this.$router.push({
path: "/indexFull/goodsFull/fbPingjia",
query: {
orderid,
itemid
}
});
},

async showdata() {
let data = {
plat: 3,
account: this.account,
token: this.token,
order_id: this.$route.query.order_id,
order_sn: this.$route.query.order_sn
};
const [err, res] = await api.neworderdetail(data);
if (res) {
console.log(res);
switch (res.data.order_top_status) {
case 4:
this.isAfter = 1;
break;
case 5:
this.isAfter = 1;
break;
case 6:
this.isAfter = 1;
break;
case 11:
this.isAfter = 3;
break;
case 12:
this.isAfter = 3;
break;
case 13:
this.isAfter = 1;
break;
case 14:
this.isAfter = 1;
break;
case 15:
this.isAfter = 1;
break;
case 18:
this.isAfter = 3;
break;
case 19:
this.isAfter = 3;
break;
case 25:
this.isAfter = 3;
break;
case 7:
this.isAfter = 2;
break;
case 8:
this.isAfter = 2;
break;
case 16:
this.isAfter = 2;
break;
case 17:
this.isAfter = 2;
break;
case 23:
this.isAfter = 2;
break;
case 28:
this.isAfter = 2;
break;
case 9:
this.isAfter = 4;
break;
case 10:
this.isAfter = 4;
break;
case 24:
this.isAfter = 4;
break;
case 27:
this.isAfter = 4;
break;
case 20:
this.isAfter = 1;
break;
case 21:
this.isAfter = 1;
break;
case 22:
this.isAfter = 1;
break;
default:
this.isAfter = 0;
break;
}
console.log(res.data);
this.infodata = res.data;
this.isAfter = this.isAfter;
}











},
...mapActions(["update_storeInfo"])
},
computed: {
...mapGetters(["user", "account", "token"])
}
};



